body {
    background-color: wheat;
}

.App-wrapper {
    display: grid;
    grid-template-rows: 50px 11fr;
    grid-template-columns: 16% 50%;
    grid-gap: 5px;
    justify-content: center;
    grid-template-areas:
            'h h '
            'n c';
}

.app-wrapper-content {
    grid-area: c;
    background: slateblue;
}




